<template>
      <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                        <slot name="header">
                            <h5 class="modal-title">{{$t('Generatepubliclink')}}</h5>
                        </slot>
                        <button type="button" v-on:click="$emit('close')" href="javascript:;" class="close">
                            <em class="fa fa-times"></em> 
                        </button>
                </div>
                <div class="modal-body">
                    <div class="container-fluid">
                        <div class="row">
                            <label>Name:</label>
                            <div class="input-group">
                                <input type="text" class="form-control" v-model="folderOrFileName" disabled>
                            </div>
                        </div><br>
                        <div class="row">
                            <div class="col-lg-6 d-flex align-items-center">
                                <label> Expiration:&nbsp</label>
                                <label v-bind:class="{ 'switch': true, 'switch enabled': (isTrueCheck == true) }">
                                    <input type="checkbox" @click="userLoggedIn = !userLoggedIn" 
                                    v-bind:checked="isTrueCheck" v-bind:id="'isTrueCheck'"
                                    v-on:change="ChangeCheckValue(item, $event)"
                                    class="form-check-input rdoticketfor custom-control-input dynamic">
                                    <span class="slider round">
                                        <span class="slider-yes">Yes</span>
                                        <span class="slider-no">No</span>
                                    </span>
                                </label>
                            </div>
                            <div class="col-lg-6" v-if="showDatePicker">
                                <v-date-picker  :popover="popover" :min-date='new Date()'
                                    v-model="ExpirationDate" class="input-group" placeholder="Enter expiration date">
                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                        <input class="form-control" placeholder="Enter expiration date" :value="inputValue" v-on="inputEvents" />
                                        <div @click="togglePopover()" class="input-group-append"><span
                                                class="input-group-text"><em class="fa fa-calendar"></em> </span>
                                        </div>
                                    </template>
                                </v-date-picker>
                            </div>
                        </div>
                        <div class="row flex-row-reverse">
                            <div class="col-lg-6 text-right mt-2">
                                <a href="javascript:;" title="Generate and CopyLink" @click="GeneratePublicLink" class="btn btn-success save-btn mr-1"><em class="fa fa-link pr-2"></em>Generate</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
      </div>
</template> 
<script>
import DataService from '../../../services/DataService';
export default{
    props: {
        fileOrFolderId: {
            type: String,
            required: true
        },
        isDocCode: {
            type: String,
            required: true
        },
        folderOrFileName: {
            type: String,
            required: true
        },
        fileOrFolderDetail: {
            type: Array,
            required: true
        }
    },
    data(){
        return {
            showDatePicker:false
        }
    },
    methods:{
        GeneratePublicLink:function(){
            var vm=this;
            var generateJSONData=[];
            vm.isLoading = true;
            let obj = {
                SourceId: vm.fileOrFolderId,
                RefCode: vm.isDocCode,
                ExpirationLink:vm.ExpirationDate
            }
            generateJSONData.push(obj);
            var params = `docJsonData=${JSON.stringify(generateJSONData)}`;
            DataService.GenerateDocPublicLink(params).then((response) => {
                vm.ShowAlert(vm.$t('GenerateLinkSuccessfullyGenerated'));
                vm.isLoading = false;
                vm.$emit("StorageDocumentListing",0,'My Space')
                vm.CopyLink();
                vm.$emit("close")
            });
        },
        CopyLink(itm,doccode) {
            var vm = this;
            var EncryptFileId = btoa(vm.fileOrFolderDetail.Id);
            if(vm.fileOrFolderDetail.IsDocCode==0){
                var params = 'FolderId=' + EncryptFileId + '&UniqueCode=' + vm.fileOrFolderDetail.UniqueCode + '&FolderName=' + vm.fileOrFolderDetail.NAME + ''
                var pathname = window.location.origin + '/DownloadPublicFolder?' + params + '';
            }
            else{
                var pathname = window.location.origin+'/DownloadPublicFile?FileId='+EncryptFileId;
            }
           
            navigator.clipboard.writeText(pathname);
            vm.showTool()
        },
        showTool() {
            this.tool = true
            setTimeout(() => this.tool = false, 1000)
        },
        ChangeCheckValue:function(){
            var vm=this;
            var x = $("#isTrueCheck").is(":checked");
            if (x == true) {
                vm.showDatePicker=true;
            }
            else {
                this.ExpirationChecked = 0;
                this.ExpirationDate = null;
                vm.showDatePicker=false;
            }
        }
    }
}
</script>
